/* ---------- Section Wrapper ---------- */
.review-section {
  margin-top: 30px;
  padding: 0 12px;
}

/* ---------- Card ---------- */
.review-card {
  background: #ffffff;
  border-radius: 16px;
  padding: 22px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.04);
  max-width: 100%;
}

/* ---------- Title ---------- */
.review-title {
  font-size: 18px;
  font-weight: 700;
  color: #111827;
  margin-bottom: 18px;
}

/* ---------- Form Group ---------- */
.review-group {
  margin-bottom: 18px;
}

.review-group label {
  display: block;
  font-size: 13px;
  font-weight: 600;
  color: #374151;
  margin-bottom: 6px;
}

.review-group label span {
  color: #9ca3af;
  font-weight: 500;
}

/* ---------- Stars ---------- */
.review-stars {
  display: flex;
  gap: 6px;
  margin: 6px 0;
}

.review-star {
  font-size: 24px;
  color: #e5e7eb;
  cursor: pointer;
  transition: all 0.2s ease;
}

.review-star:hover {
  transform: scale(1.1);
  color: #f59e0b;
}

.review-star.filled {
  color: #fbbf24;
}

/* ---------- Textarea ---------- */
.review-group textarea {
  width: 100%;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  padding: 12px;
  font-size: 14px;
  resize: none;
  outline: none;
  transition: all 0.2s ease;
}

.review-group textarea:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

/* ---------- Helper Text ---------- */
.review-group small {
  display: block;
  margin-top: 6px;
  font-size: 11px;
  color: #9ca3af;
}

/* ---------- Submit Button ---------- */
.review-submit {
  margin-top: 10px;
  background: #1d72f3;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.review-submit:hover {
  background: #155bd5;
}

.review-submit:disabled {
  background: #9ca3af;
  cursor: not-allowed;
}

/* ---------- Desktop Layout ---------- */
@media (min-width: 992px) {
  .review-section {
    padding: 0;
  }

  .review-card {
    padding: 26px 28px;
  }

  .review-title {
    font-size: 20px;
  }
}

/* ---------- Mobile Layout ---------- */
@media (max-width: 500px) {
  .review-section {
    padding: 0;
  }
  .review-card {
    padding: 10px;
  }

  .review-title {
    font-size: 16px;
  }
}
